<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!--  -->
      <div class="search">
        <el-input
          @clear="loadUserList()"
          clearable
          placeholder=""
          v-model="query"
          class="input-with-select"
        >
          <el-button slot="append" icon="el-icon-search" @click="searchUsers()"></el-button>
        </el-input>
        <el-button type="success" plain @click="showAddUserDia()"></el-button>
      </div>
      <!--  -->
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column type="index" width="50"></el-table-column>
        <el-table-column prop="username" label="" width="180"></el-table-column>
        <el-table-column prop="email" label="" width="180"></el-table-column>
        <el-table-column prop="mobile" label=""></el-table-column>
        <el-table-column label="">
          <!-- template -->
          <!-- templatev-slotcreate_timetableData -->
          <!-- tableData.row -->
          <template v-slot="tableData">{{tableData.row.create_time | formatDate}}</template>
        </el-table-column>
        <el-table-column prop="mg_state" label="">
          <template v-slot="scope">
            <el-switch
              @change="changeUserState(scope.row)"
              v-model="scope.row.mg_state"
              active-color="#13ce66"
              inactive-color="#ff4949"
            ></el-switch>
          </template>
        </el-table-column>
        <el-table-column prop="address" label="">
          <template v-slot="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              circle
              @click="showEditUserDia(scope.row)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              circle
              @click="showDelUserMsgBox(scope.row.id)"
            ></el-button>
            <el-button
              type="success"
              icon="el-icon-setting"
              circle
              @click="showSetUserRoleDia(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagenum"
        :page-sizes="[2, 5, 10]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <!--  -->
      <!--  -->
      <el-dialog title="" :visible.sync="dialogFormVisibleAdd">
        <el-form :model="addForm" :rules="addFormRules">
          <el-form-item label="" label-width="formLabelWidth">
            <el-input v-model="addForm.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label=" " label-width="formLabelWidth">
            <el-input v-model="addForm.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label=" " label-width="formLabelWidth">
            <el-input v-model="addForm.email" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label=" " label-width="formLabelWidth">
            <el-input v-model="addForm.mobile" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisibleAdd = false"> </el-button>
          <el-button type="primary" @click="addUser() "> </el-button>
        </div>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogFormVisibleEdit">
        <el-form :model="editForm" :rules="editFormRules">
          <el-form-item label="" label-width="formLabelWidth">
            <el-input disabled v-model="editForm.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label=" " label-width="formLabelWidth">
            <el-input v-model="editForm.email" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label=" " label-width="formLabelWidth">
            <el-input v-model="editForm.mobile" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisibleEdit = false"> </el-button>
          <el-button type="primary" @click="editUser()"> </el-button>
        </div>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogFormVisibleRole">
        <el-form :model="form">
          <el-form-item label="" label-width="100px">{{currUserName}}</el-form-item>
          <el-form-item label="" label-width="100px">
            <el-select v-model="currRoleId" placeholder="">
              <el-option label="" :value="-1"></el-option>
              <el-option :label="item.roleName" :value="item.id" v-for="(item,i) in roles" :key="i"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisibleRole = false"> </el-button>
          <el-button type="primary" @click="setRole()"> </el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import { log } from "util";
export default {
  components: {},
  data() {
    //  data()  return 
    let checkEmail = (rule, value, callback) => {
      if (
        /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(value) ===
        false
      ) {
        return callback(new Error(""));
      }
      // 
      callback();
    };

    // 
    let checkMobile = (rule, value, callback) => {
      if (/^1\d{10}$/.test(value) === false) {
        return callback(new Error(""));
      }
      callback();
    };
    return {
      tableData: [],
      query: "",
      dialogFormVisibleAdd: false,
      addForm: {
        username: "",
        password: "",
        email: "",
        mobile: ""
      },
      addFormRules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 6, message: " 3  6 ", trigger: "blur" }
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 15, message: " 6  15 ", trigger: "blur" }
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" }
        ]
      },
      dialogFormVisibleEdit: false,
      editForm: {
        id: "",
        username: "",
        email: "",
        mobile: ""
      },
      // 
      editFormRules: {
        email: [
          { required: true, message: "", trigger: "blur" },
          // 
          { validator: checkEmail, trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          // 
          { validator: checkMobile, trigger: "blur" }
        ]
      },
      dialogFormVisibleRole: false,
      form: {
        id: "", // Id
        role_name: "", // 
        username: "", // 
        rid: "" // Id
      },
      // 
      total: -1,
      pagenum: 1,
      pagesize: 5,
      // 
      currRoleId: -1,
      currUserId: -1,
      currUserName: "",
      // 
      roles: []
    };
  },
  methods: {
    getUsers() {
      this.axios
        .get("users", {
          params: {
            query: this.query,
            pagenum: this.pagenum,
            pagesize: this.pagesize
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.meta.status !== 200) {
            return this.$message.error("");
          }
          this.tableData = res.data.data.users;
          this.total = res.data.data.total;
        });
    },
    searchUsers() {
      this.getUsers();
    },
    addUser() {
      this.dialogFormVisibleAdd = false;
      this.axios.post("users", this.addForm).then(res => {
        if (res.data.meta.status === 201) {
          this.$message.success(res.data.meta.msg);
          this.getUsers();
          this.form = {};
        } else {
          this.$message.error("");
        }
      });
    },
    editUser() {
      this.axios.put(`users/${this.editForm.id}`, this.editForm).then(res => {
        if (res.data.meta.status !== 200) {
          return this.$message.error("");
        }
        this.$message.success("");
        this.dialogFormVisibleEdit = false;
        this.getUsers();
      });
    },
    setRole() {
      this.axios
        .put(`users/${this.currUserId}/role`, { rid: this.currRoleId })
        .then(res => {
          if (res.data.meta.status !== 200) {
            return this.$message.error("");
          }
          this.$message.success("");
          this.dialogFormVisibleRole = false;
          this.getUsers();
        });
    },
    changeUserState(user) {
      this.axios.put(`users/${user.id}/state/${user.mg_state}`).then(res => {
        if (res.data.meta.status !== 200) {
          return this.$message.error("");
        }
        this.$message.success("");
      });
    },
    showAddUserDia() {
      this.addForm = {};
      this.dialogFormVisibleAdd = true;
    },
    showDelUserMsgBox(userId) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          const res = await this.axios.delete(`users/${userId}`);
          if (res.data.meta.status === 200) {
            this.pagenum = 1;
            this.getUsers();
            this.$message({
              type: "success",
              message: "!"
            });
          } else {
            this.$message.error("");
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    showEditUserDia(user) {
      this.editForm = user;
      this.dialogFormVisibleEdit = true;
    },
    showSetUserRoleDia(user) {
      this.currUserName = user.username;
      this.currUserId = user.id;
      // 
      this.axios.get(`roles`).then(res => {
        this.roles = res.data.data;
      });
      // id
      this.axios.get(`users/${user.id}`).then(res => {
        this.currRoleId = res.data.data.rid;
      });
      this.dialogFormVisibleRole = true;
    },
    loadUserList() {
      this.getUsers();
    },
    handleSizeChange(val) {
      // console.log(` ${val} `);
      this.pagesize = val;
      this.pagenum = 1;
      this.getUsers();
    },
    handleCurrentChange(val) {
      // console.log(`: ${val}`);
      this.pagenum = val;
      this.getUsers();
    }
  },
  created() {
    this.getUsers();
  }
};
</script>

<style lang='less' scoped>
.search {
  margin: 10px 0;
  .el-input {
    width: 47%;
    margin-right: 5px;
  }
}
</style>
