<template>
  <div>
    <el-card>
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <el-row>
        <el-col :span="8">
          <el-input
            placeholder=""
            class="input-with-select"
            v-model="queryinfo.query"
            clearable
          >
            <el-button slot="append" icon="el-icon-search" @click="getOrderList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <el-table :data="orderList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="order_number" width="200"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="" prop="pay_status">
          <template v-slot="scope">
            <el-tag type="danger" v-if="scope.row.pay_status === '0'" size="small"></el-tag>
            <el-tag type="success" v-else size="small"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>
        <el-table-column label="" prop="create_time" width="140">
          <template v-slot="scope">{{scope.row.create_time | formatDate}}</template>
        </el-table-column>
        <el-table-column label="" width="140">
          <template v-slot="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              circle
              title=""
              @click="showEditDia"
            ></el-button>
            <el-button
              type="success"
              icon="el-icon-location"
              circle
              title=""
              @click="showLogisticsDialog"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <el-dialog title="" :visible.sync="dialogFormVisible" width="50%">
      <el-form :model="addressForm" :rules="addressFormRules">
        <el-form-item label="/" prop="address1">
          <!--  -->
          <el-cascader
            expand-trigger="hover"
            clearable
            :options="cityData"
            v-model="selectedOptions"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogLogisticsVisible" width="50%">
      <el-steps direction="vertical" :active="1">
        <el-step
          v-for="(item, i) in logisticsInfo"
          :key="i"
          :title="item.time"
          :description="item.context"
        ></el-step>
      </el-steps>
    </el-dialog>
  </div>
</template>

<script>
import cityData from "@/assets/city_data.js";

export default {
  components: {},
  data() {
    return {
      queryinfo: {
        query: "",
        pagenum: 1,
        pagesize: 10
      },
      orderList: [],
      // 
      pagenum: 1,
      pagesize: 10,
      total: 0,
      // 
      dialogFormVisible: false,
      addressForm: {
        address: ""
      },
      addressFormRules: {
        address1: [
          { required: true, message: "", trigger: "blur" }
        ],
        address2: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      cityData: [],
      selectedOptions: [],
      // 
      dialogLogisticsVisible: false,
      logisticsInfo: []
    };
  },
  created() {
    this.getOrderList();
  },
  methods: {
    getOrderList() {
      this.axios
        .get(`orders/?pagenum=${this.pagenum}&pagesize=${this.pagesize}`)
        .then(res => {
          this.orderList = res.data.data.goods;
          this.total = res.data.data.total;
        });
    },
    showEditDia() {
      this.dialogFormVisible = true;
      this.cityData = cityData;
    },
    showLogisticsDialog() {
      this.axios.get("/kuaidi/619915933338").then(res => {
        if (res.data.meta.status !== 200) {
          return this.$message.error("");
        }
        this.logisticsInfo = res.data;
        this.dialogLogisticsVisible = true;
      });
    },
    handleSizeChange(val) {
      this.pagesize = val;
      this.pagenum = 1;
      this.getOrderList();
    },
    handleCurrentChange(val) {
      this.pagenum = val;
      this.getOrderList();
    }
  }
};
</script>

<style lang='less' scoped>
.el-breadcrumb {
  margin-bottom: 15px;
}
.el-table {
  margin: 15px 0;
}
</style>
