<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-button class="addRoleBtn" type="success" plain @click="showAddRoleDia()"></el-button>
      <el-table :data="roleList" border style="width: 100%">
        <el-table-column type="expand" width="150">
          <template v-slot="scope">
            <el-row v-for="(level1, i) in scope.row.children" :key="i">
              <el-col :span="4">
                <el-tag
                  @close="deleRight(scope.row, level1.id)"
                  closable
                  type="success"
                >{{level1.authName}}</el-tag>
                <i class="el-icon-arrow-right"></i>
              </el-col>
              <el-col :span="20">
                <el-row v-for="(level2, i) in level1.children" :key="i">
                  <el-col :span="4">
                    <el-tag
                      @close="deleRight(scope.row, level3.id)"
                      closable
                      type
                    >{{level2.authName}}</el-tag>
                    <i class="el-icon-arrow-right"></i>
                  </el-col>
                  <el-col :span="20">
                    <el-tag
                      @close="deleRight(scope.row, level3.id)"
                      closable
                      type="warning"
                      v-for="(level3, i) in level2.children"
                      :key="i"
                    >{{level3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
            <span v-if="scope.row.children.length === 0"></span>
          </template>
        </el-table-column>
        <el-table-column type="index" label="#" width="100"></el-table-column>
        <el-table-column prop="roleName" label="" width="200"></el-table-column>
        <el-table-column prop="roleDesc" label="" width="250"></el-table-column>
        <el-table-column prop="address" label="">
          <template v-slot="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              circle
              @click="showEditRoleDia(scope.row)"
            ></el-button>
            <el-button type="danger" icon="el-icon-delete" circle @click="removeRole(scope.row)"></el-button>
            <el-button
              type="success"
              icon="el-icon-setting"
              circle
              @click="showSetRightDia(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-dialog title="" :visible.sync="diaFormVisibleAdd" width="50%">
        <!--  -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
          <el-form-item label="" prop="roleName">
            <el-input v-model="addForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="addForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="diaFormVisibleAdd = false"> </el-button>
          <el-button type="primary" @click="addRole"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisibleEdit" width="50%">
        <!--  -->
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="80px">
          <el-form-item label="" prop="roleName">
            <el-input v-model="editForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="editForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisibleEdit = false"> </el-button>
          <el-button type="primary" @click="editRole"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogFormVisibleRight">
        <!-- 
      show-checkbox -> 
      node-key 
      default-expand-keys  [id]
      default-checked-keys  [id]
      props  {label, children}
      labelchildren
        datakey 'label' 'chldren'-->
        <!-- 
        -->
        <el-tree
          ref="tree"
          :data="treeList"
          show-checkbox
          node-key="id"
          :props="defaultProps"
          default-expand-all
          :default-checked-keys="arrCheck"
        ></el-tree>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisibleRight = false"> </el-button>
          <el-button type="primary" @click="setRoleRight"> </el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      roleList: [],
      diaFormVisibleAdd: false,
      // 
      addForm: {
        roleName: "",
        roleDesc: ""
      },
      // 
      addFormRules: {
        // 
        roleName: [
          { required: true, message: "", trigger: "blur" }
        ],
        roleDesc: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      // 
      dialogVisibleEdit: false,
      // 
      editForm: {
        id: "",
        roleName: "",
        roleDesc: ""
      },
      // 
      editFormRules: {
        // 
        roleName: [
          { required: true, message: "", trigger: "blur" }
        ],
        roleDesc: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      // 
      dialogFormVisibleRight: false,
      treeList: [],
      defaultProps: {
        label: "authName",
        childrem: "children"
      },
      arrExpand: [],
      arrCheck: [],
      currRoleId: -1
    };
  },
  methods: {
    getRoleList() {
      this.axios.get(`roles`).then(res => {
        this.roleList = res.data.data;
      });
    },
    // 
    showAddRoleDia() {
      this.diaFormVisibleAdd = true;
    },
    addRole() {
      this.diaFormVisibleAdd = false;
      this.axios.post("roles", this.addForm).then(res => {
        if (res.data.meta.status === 201) {
          this.$message.success(res.data.meta.msg);
          this.getRoleList();
          this.form = {};
        } else {
          this.$message.error("");
        }
      });
    },
    // 
    showEditRoleDia(role) {
      this.editForm = role;
      this.dialogVisibleEdit = true;
    },
    editRole() {
      this.axios.put(`roles/${this.editForm.id}`, this.editForm).then(res => {
        console.log(res);
        if (res.data.meta.status !== 200) {
          return this.$message.error("");
        }
        this.$message.success("");
        this.getRoleList();
        this.dialogVisibleEdit = false;
      });
    },
    // 
    removeRole(roleId) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          const res = await this.axios.delete(`roles/${roleId.id}`);
          if (res.data.meta.status === 200) {
            // this.pagenum = 1;
            this.getRoleList();
            this.$message({
              type: "success",
              message: "!"
            });
          } else {
            this.$message.error("");
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    // 
    deleRight(role, rightId) {
      this.axios.delete(`roles/${role.id}/rights/${rightId}`).then(res => {
        role.children = res.data.data;
      });
    },
    showSetRightDia(role) {
      this.currRoleId = role.id;
      this.axios.get(`rights/tree`).then(res => {
        console.log(res);

        this.treeList = res.data.data;
      });
      // 
      let arrtemp2 = [];
      role.children.forEach(item1 => {
        // arrtemp2.push(item1.id);
        item1.children.forEach(item2 => {
          // arrtemp2.push(item2.id);
          item2.children.forEach(item3 => {
            arrtemp2.push(item3.id);
          });
        });
      });
      this.arrCheck = arrtemp2;
      this.dialogFormVisibleRight = true;
    },
    setRoleRight() {
      let arr1 = this.$refs.tree.getCheckedKeys();
      let arr2 = this.$refs.tree.getHalfCheckedKeys();
      let arr = [...arr1, ...arr2];
      this.axios
        .post(`roles/${this.currRoleId}/rights`, { rids: arr.join(",") })
        .then(res => {
          console.log(res);
        });
      this.getRoleList();
      this.dialogFormVisibleRight = false;
    }
  },
  created() {
    this.getRoleList();
  }
};
</script>

<style lang='less' scoped>
.addRoleBtn {
  display: block;
  margin: 0 0 15px 15px;
}
</style>
