<template>
  <el-card class>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-row>
      <el-col :span="24" class="searchArea">
        <el-button type="primary" @click="addGoodCate()"></el-button>
      </el-col>
    </el-row>
    <!--  -->
    <el-dialog title="" :visible.sync="diaFormVisibleAdd">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.cat_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-cascader
            v-model="selectedOptions"
            :options="casList"
            expand-trigger="hover"
            :props="defaultProp"
            change-on-select
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="diaFormVisibleAdd = false"> </el-button>
        <el-button type="primary" @click="addCate() "> </el-button>
      </div>
    </el-dialog>

    <el-table :data="tableList" style="width: 100%">
      <!-- 
        treeKey  id
        parentKey id
        levelKey 
        childKey 
      -->
      <el-tree-grid
        prop="cat_name"
        label=""
        treeKey="cat_id"
        parentKey="cat_pid"
        levelKey="cat_level"
        childKey="children"
      ></el-tree-grid>
      <el-table-column label="">
        <template v-slot="scope">
          <span v-if="scope.row.cat_level===0"></span>
          <span v-else-if="scope.row.cat_level===1"></span>
          <span v-else-if="scope.row.cat_level===2"></span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template v-slot="scope">
          <span v-if="scope.row.cat_deleted===false"></span>
          <span v-else-if="scope.row.cat_deleted===true"></span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template v-slot="scope">
          <el-button type="primary" icon="el-icon-edit" circle @click="showEditDialog(scope.row)"></el-button>
          <el-button type="danger" icon="el-icon-delete" circle @click="remove(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisibleEdit" width="50%">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item label="" prop="cat_name">
          <el-input v-model="editForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibleEdit = false"> </el-button>
        <el-button type="primary" @click="saveCate"> </el-button>
      </span>
    </el-dialog>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pagenum"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pagesize"
      layout="total, prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </el-card>
</template>

<script>
// element-tree-grid
var ElTreeGrid = require("element-tree-grid");
export default {
  components: {
    ElTreeGrid
  },
  data() {
    return {
      tableList: [],
      pagenum: 1,
      pagesize: 10,
      total: 1,
      // 
      diaFormVisibleAdd: false,
      form: {
        cat_pid: -1,
        cat_name: "",
        cat_level: -1
      },
      formLabelWidth: "140px",
      casList: [],
      selectedOptions: [],
      defaultProp: {
        value: "cat_id",
        label: "cat_name",
        children: "children"
      },
      // 
      dialogVisibleEdit: false,
      editForm: {
        id: "",
        cat_name: ""
      },
      // 
      editFormRules: {
        cat_name: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.getGoodsCate();
  },
  methods: {
    getGoodsCate() {
      this.axios
        .get(
          `categories?type=3&pagenum=${this.pagenum}&pagesize=${this.pagesize}`
        )
        .then(res => {
          this.tableList = res.data.data.result;
          this.total = res.data.data.total;
        });
    },
    showEditDialog(scope) {
      this.axios.get(`categories/${scope.cat_id}`).then(res => {
        if (res.data.meta.status !== 200)
          return this.$message.error("");
        this.editForm = res.data.data;
      });
      this.dialogVisibleEdit = true;
    },
    saveCate() {
      this.axios
        .put(`categories/${this.editForm.cat_id}`, {
          cat_name: this.editForm.cat_name
        })
        .then(res => {
          if (res.data.meta.status !== 200)
            return this.$message.error("");
          this.$message.success("");
          this.getGoodsCate();
          this.dialogVisibleEdit = false;
        });
    },
    remove(scope) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          const res = await this.axios.delete(`categories/${scope.cat_id}`);
          if (res.data.meta.status === 200) {
            this.$message({
              type: "success",
              message: "!"
            });
            this.getGoodsCate();
          } else {
            this.$message.error("");
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    addCate() {
      if (this.selectedOptions.length === 0) {
        this.form.cat_pid = 0;
        this.form.cat_level = 0;
      } else if (this.selectedOptions.length === 1) {
        this.form.cat_pid = this.selectedOptions[0];
        this.form.cat_level = 1;
      } else if (this.selectedOptions.length === 2) {
        this.form.cat_pid = this.selectedOptions[1];
        this.form.cat_level = 2;
      }
      this.axios.post(`categories`, this.form).then(res => {
        console.log(res);
      });
      this.getGoodsCate();
      this.diaFormVisibleAdd = false;
      this.form = {};
    },
    addGoodCate() {
      this.axios.get(`categories?type=2`).then(res => {
        this.casList = res.data.data;

        this.diaFormVisibleAdd = true;
      });
    },
    handleSizeChange(val) {
      this.pagesize = val;
      this.pagenum = 1;
      this.getGoodsCate();
    },
    handleCurrentChange(val) {
      this.pagenum = val;
      this.getGoodsCate();
    }
  }
};
</script>

<style lang='less' scoped>
.el-row {
  text-align: left;
  margin-bottom: 15px;
}
</style>
