<template>
  <el-card>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-alert title="" type="error"></el-alert>
    <el-row>
      <el-col>
        
        <!--  -->
        <el-cascader
          :show-all-levels="false"
          v-model="selectedOptions"
          :options="options"
          expand-trigger="hover"
          :props="defaultProp"
          @change="handleChange"
          clearable
        ></el-cascader>
      </el-col>
    </el-row>
    <el-tabs type="card" v-model="activeName" @tab-click="handleClick()">
      <el-tab-pane label="" name="many">
        <el-button type="primary" @click="setDyParams"></el-button>
        <el-table :data="arrDyparams" style="width: 100%">
          <el-table-column type="expand" label="#">
            <template v-slot="scope">
              <el-tag
                :key="tag"
                v-for="tag in scope.row.attr_vals"
                closable
                :disable-transitions="false"
                @close="handleClose(scope.row,tag)"
              >{{tag}}</el-tag>
              <el-input
                class="input-new-tag"
                v-if="inputVisible"
                v-model="inputValue"
                ref="saveTagInput"
                size="small"
                @keyup.enter.native="handleInputConfirm(scope.row.attr_vals)"
                @blur="handleInputConfirm(scope.row)"
              ></el-input>
              <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
            </template>
          </el-table-column>
          <el-table-column label="" prop="attr_name"></el-table-column>
          <el-table-column label="">
            <template v-slot="scope">
              <el-button type="primary" icon="el-icon-edit" circle @click="showEditDialog(scope)"></el-button>
              <el-button type="danger" icon="el-icon-delete" circle @click="remove(scope)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="" name="only">
        <el-button type="primary" @click="setStaticParams()"></el-button>
        <el-table :data="arrStaticparams" style="width: 100%">
          <el-table-column type="expand" label="#">
            <template v-slot="scope">
              <el-tag
                :key="i"
                v-for="(tag, i) in scope.row.attr_vals"
                closable
                :disable-transitions="false"
                @close="handleClose(scope, i)"
              >{{tag}}</el-tag>
              <el-input
                class="input-new-tag"
                v-if="inputVisible"
                v-model="inputValue"
                ref="saveTagInput"
                size="small"
                @keyup.enter.native="handleInputConfirm(scope.row.attr_vals)"
                @blur="handleInputConfirm(scope.row)"
              ></el-input>
              <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
            </template>
          </el-table-column>
          <el-table-column label="" prop="attr_name"></el-table-column>
          <el-table-column label="" prop="attr_vals"></el-table-column>
          <el-table-column label="">
            <template v-slot="scope">
              <el-button type="primary" icon="el-icon-edit" circle @click="showEditDialog(scope)"></el-button>
              <el-button type="danger" icon="el-icon-delete" circle @click="remove(scope)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
    <!--  -->
    <el-dialog
      :title="activeName === 'many' ? '' : ''"
      :visible.sync="addDialogVisible"
      width="50%"
    >
      <el-form :model="addForm" :rules="addFormRules" label-width="100px">
        <el-form-item :label="activeName === 'many' ? '' : ''" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParams"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      :title="activeName === 'many' ? '' : ''"
      :visible.sync="editDialogVisible"
      width="50%"
    >
      <el-form :model="editForm" :rules="editFormRules" label-width="100px">
        <el-form-item :label="activeName === 'many' ? '' : ''" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editParams"> </el-button>
      </span>
    </el-dialog>
  </el-card>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      // 
      options: [],
      selectedOptions: [],
      defaultProp: {
        label: "cat_name",
        value: "cat_id",
        children: "children"
      },
      activeName: "many",
      tableList: [],
      // 
      addDialogVisible: false,
      addForm: {
        attr_name: ""
      },
      addFormRules: {
        attr_name: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      // 
      editDialogVisible: false,
      editForm: {
        attrId: "", // Id
        attr_name: "" // 
      },
      editFormRules: {
        attr_name: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      arrDyparams: [],
      arrStaticparams: [],
      inputVisible: false,
      inputValue: ""
    };
  },
  created() {
    this.getGoodCate();
  },
  methods: {
    handleChange() {
      if (this.selectedOptions.length === 3) {
        this.axios
          .get(`categories/${this.selectedOptions[2]}/attributes?sel=many`)
          .then(res => {
            this.arrDyparams = res.data.data;
            this.arrDyparams.forEach(item => {
              item.attr_vals =
                item.attr_vals.length === 0
                  ? []
                  : item.attr_vals.trim().split(",");
            });
          });
      }
    },
    getGoodCate() {
      this.axios.get(`categories?type=3`).then(res => {
        this.options = res.data.data;
      });
    },
    handleClick() {
      if (this.activeName === "only") {
        if (this.selectedOptions.length === 3) {
          this.axios
            .get(`categories/${this.selectedOptions[2]}/attributes?sel=only`)
            .then(res => {
              this.arrStaticparams = res.data.data;
              //   attr_vals   split  attr_vals    
              res.data.data.forEach(item => {
                //  item.attr_vals    
                //  
                item.attr_vals = item.attr_vals
                  ? item.attr_vals.split(" ")
                  : [];
                item.inputVisible = false;
                item.inputValue = "";
              });
              //   activeName 
              if (this.activeName === "many") {
                this.arrDyparams = res.data.data;
              } else {
                this.arrStaticparams = res.data.data;
              }
            });
        }
      }
    },
    setDyParams() {
      this.addDialogVisible = true;
    },
    setStaticParams() {
      this.addDialogVisible = true;
    },
    addParams() {
      this.axios
        .post(`categories/${this.selectedOptions[2]}/attributes`, {
          attr_name: this.addForm.attr_name,
          attr_sel: this.activeName // 
        })
        .then(res => {
          console.log(res);
          if (res.data.meta.status !== 201)
            return this.$message.error("");
          this.$message.success("");
          // 
          this.getGoodCate();
          // 
          this.addDialogVisible = false;
        });
    },
    // x
    handleClose(scope, tag) {
      scope.attr_vals.splice(scope.attr_vals.indexOf(tag), 1);
      let putData = {
        attr_name: scope.attr_name,
        attr_sel: "many",
        attr_vals: scope.attr_vals.join(",")
      };
      this.axios
        .put(
          `categories/${this.selectedOptions[2]}/attributes/${scope.attr_id}`,
          putData
        )
        .then(res => {
          // console.log(res);
        });
    },
    // newTag+
    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    // +
    handleInputConfirm(scope) {
      let inputValue = this.inputValue;
      if (inputValue) {
        scope.attr_vals.push(inputValue);

        let putData = {
          attr_name: scope.attr_name,
          attr_sel: "many",
          attr_vals: scope.attr_vals.join(",")
        };
        this.axios
          .put(
            `categories/${this.selectedOptions[2]}/attributes/${scope.attr_id}`,
            putData
          )
          .then(res => {
            // console.log(res);
          });
      }

      this.inputVisible = false;
      this.inputValue = "";
    },
    showEditDialog(scope) {
      this.axios
        .get(
          `categories/${this.selectedOptions[2]}/attributes/${
            scope.row.attr_id
          }`
        )
        .then(res => {
          if (res.data.meta.status !== 200)
            return this.$message.error("");
          //  data  editForm 
          this.editForm.attr_name = res.data.data.attr_name;
          this.editForm.attrId = res.data.data.attr_id;
          //  
          this.editDialogVisible = true;
        });
    },
    editParams() {
      this.axios
        .put(
          `categories/${this.selectedCate[2]}/attributes/${
            this.editForm.attrId
          }`,
          {
            attr_name: this.editForm.attr_name,
            attr_sel: this.activeName
          }
        )
        .then(res => {
          if (res.data.meta.status !== 200)
            return this.$message.error("");
          this.$message.success("");
          this.getGoodCate();
          this.editDialogVisible = false;
        });
    },
    remove(scope) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(async () => {
        const res = await this.axios.delete(
          `categories/${this.selectedOptions[2]}/attributes/${
            scope.row.attr_id
          }`
        );
        if (res.data.meta.status === 200) {
          this.getGoodCate();
          this.$message({
            type: "success",
            message: "!"
          });
        } else {
          this.$message.error("");
        }
      });
    }
  }
};
</script>

<style lang='less' scoped>
// .alert {
//   margin-bottom: 20px;
// }
.el-row {
  margin: 20px 0;
  text-align: left;
}
.el-tabs {
  text-align: left;
}
.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
</style>
